<template>
  <main class="App">
      <div class="flex-container">
        <div class="logo-section">
          <img src="../images/logo.svg" alt="" />
        </div>
        <div class="heading-section">
          <h1>
            We are launching <b>soon!</b>
          </h1>
          <h2>Subscribe and get notified</h2>
        </div>
        <div class="form-section">
          <form @submit.prevent="">
            <input ref="inputField" :class="[valid ? 'errorBorder' : 'normalBorder']" type="text" placeholder="Your email address..." />
            <button type="submit" @click="validate">Notify Me</button>
          </form>
        </div>
        <div class="error-section">
          <span v-if="valid">Please provide a valid email address</span>
        </div>
        <div class="image-section">
          <img src="../images/illustration-dashboard.png" alt="" />
        </div>
        <footer>
          <ul>
            <li><a href="#"><font-awesome-icon icon="fa-brands fa-facebook-f" /></a></li>
            <li><a href="#"><font-awesome-icon icon="fa-brands fa-twitter" /></a></li>
            <li><a href="#"><font-awesome-icon icon="fa-brands fa-instagram" /></a></li>
          </ul>
          <p>&copy; Copyright Ping. All rights reserved.</p>
        </footer>
      </div>
    </main>
</template>

<script>

export default {
  name: 'App',
  data(){
    return {
      valid: false
    }
    
  },
  methods: {
    validate(){
      let validRegex = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
      if (!this.$refs.inputField.value.match(validRegex)) {
        // alert("Valid email address!");
        this.valid = true;
        
      } else {
        // alert("Invalid email address!");
        this.valid = false;
      }
    }
  }
}
</script>

